.header{
    // height: 85px;
    // border-bottom: 1px solid red;
    background-color: $white;
    position: relative;
    z-index: 20;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    &__wrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        &-logo{
            @media (max-width: 1024px){
                img{
                    width: 123px;
                }
            }
            @media (max-width: 680px){
                img{
                    width: 100px;
                }
            }
        }
        @media (max-width: 680px){
            height: 68px;
        }
    }
    &__contact{
        background-color: #f8f8f8;
        height: 85px;
        width: 300px;
        padding-left: 65px;
        transform: skew(-10deg);
        position: relative;
        &:before{
            position: absolute;
            content: "\e917";
            font-family: "icomoon";
            color: $green;
            font-size: 28px;
            left: 17px;
            top: 17px;
            transform: skew(10deg);
        }
        @media (max-width: 680px){
            display: none;
        }
        &-block{
            transform: skew(10deg);
        }

        &-data{
            color: $grey;
            font-size: 12px;
            line-height: 14px;
            font-weight: 500;
            margin-top: 15px;
            &.mobile{
                margin-top: 0px;
                font-size: 10px;
                line-height: 12px;
                padding-left: 8px;
            }
        }
        &-number{
            color: $grey;
            font-size: 24px;
            line-height: 28px;
            font-weight: 300;
            &.mobile{
                position: relative;
                padding-left: 25px;
                font-size: 14px;
                line-height: 16px;
                font-weight: 500;
                @media (max-width: 450px){
                    font-size: 12px;
                }
                &:before{
                    position: absolute;
                    content: "\e917";
                    font-family: "icomoon";
                    left: 0px;
                    top: 0px;
                    font-size: 16px;
                    color: $green;
                }
            }
        }
        &-feedback{
            color: $green;
            font-size: 14px;
            line-height: 16px;
            font-weight: 500;
            border-bottom: 1px solid $green;
            margin-top: 2px;
        }
        @media (max-width: 1024px){
            width: 210px;
            padding-left: 55px;
            .header__contact-number {
                font-size: 18px;
                line-height: 27px;
                margin-top: 3px;
            }
        }
    }
    &__name{
        display: flex;
        @media (max-width: 680px){
            margin-left: auto;
            margin-right: 20px;
        }
        &-icon{
            width: 34px;
            height: 34px;
            border: 1px solid $green;
            border-radius: 50%;
            position: relative;
            &:before{
                position: absolute;
                content: "\e920";
                font-family: "icomoon";
                font-size: 20px;
                color: $green;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
            &:after{
                position: absolute;
                content: "";
                width: 9px;
                height: 9px;
                background-color: $light-green;
                border-radius: 50%;
                bottom: 0;
                right: 0;
            }
        }
        &-wrap{
            margin-left: 10px;
            @media (max-width: 680px){
                display: none;
            }
        }
        &-item{
            font-size: 14px;
            line-height: 17px;
            color: $green;
            font-weight: 600;
        }
        &-city{
            font-size: 12px;
            line-height: 14px;
            font-weight: 300;
            color: $grey;
            margin-top: 5px;
        }
    }
    &__basket{
        width: 36px;
        height: 36px;
        background-color: #F8F8F8;
        border-radius: 50%;
        position: relative;
        &:before{
            position: absolute;
            content: "\e92b";
            font-size: 20px;
            font-family: "icomoon";
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: $green;
        }
        &:after{
            position: absolute;
            content: "3";
            text-align: center;
            width: 17px;
            height: 17px;
            background-color: $green;
            border-radius: 50%;
            right: -5px;
            bottom: -5px;
            box-shadow: 0px 0px 4px #00642F;
            @media (max-width: 680px){
                right: 0px;
            }

        }
    }
    &__mobile{
        background-color: #F8F8F8;
        &-block{
            display: flex;
            align-items: center;
            padding-top: 5px;
            padding-bottom: 5px;
            display: none;
            @media (max-width: 680px){
                display: flex;
            }
        }
    }
}

