.sidebar{
    width: 216px;
    margin-top: 15px;
    // padding-bottom: 60px;
    // position: relative;


    @media (max-width: 1024px){
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 25px;
    }
    @media (max-width: 680px){
        padding-bottom: 50px;
    }
    &__top{
        display: flex;
        margin-bottom: 40px;
        margin-left: 17px;
        @media (max-width: 1024px){
            margin: 0px;
            position: relative;
            &:after{
                position: absolute;
                content: "";
                width: 20px;
                height: 20px;
                border: 1px solid $green;
                border-radius: 50%;
                right: 0;
                top: 50%;
                transform: translate(0, -50%);
            }
            &:before{
                position: absolute;
                content: "\f107";
                font-family: "icomoon";
                right: 5px;
                top: 9px;
                font-size: 18px;
                color: $green;
                transition: transform .3s;
            }
            &.is-active{
                &:before{
                    transform: rotate(-180deg);
                }
            }
        }
        &-mobile{
            display: none;
            &-icon{
                display: flex;
            }
            &-close-btn{
                width: 40px;
                height: 40px;
                background-color: #00642f;
                border-radius: 50%;
                position: relative;
                margin-left: auto;
                &:before{
                    position: absolute;
                    content: "\e906";
                    font-family: "icomoon";
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    color: $white;
                }
            }
            @media (max-width: 680px){
                display: flex;
                // padding-left: 10px;
                // padding-right: 10px;
                // &:before,
                // &:after {
                //     display: none;
                // }
            }
        }
        
    }
    &__icon{
        width: 36px;
        height: 36px;
        border: 1px solid $green;
        border-radius: 50%;
        position: relative;
        &:before{
            position: absolute;
            content: "\e920";
            font-family: "icomoon";
            font-size: 20px;
            color: $green;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
        &:after{
            position: absolute;
            content: "";
            width: 9px;
            height: 9px;
            background-color: $light-green;
            border-radius: 50%;
            bottom: 0;
            right: 0;
        }
    }
    &__greeting{
        font-size: 14px;
        line-height: 17px;
        font-weight: 500;
        color: $green;
        // width: 100px;
        width: 115px;
        margin-left: 15px;
        @media (max-width: 680px){
            font-size: 12px;
            line-height: 14px;
        }
    }
    &__menu{
        display: flex;
        flex-direction: column;
        @media (max-width: 1024px){
            position: absolute;
            left: 50%;
            top: 195px;
            transform: translate(-50%, 0);
            z-index: 5;
            background-color: #fff;
            padding-top: 15px;
            padding-bottom: 25px;
            border-radius: 15px;
            border: 1px solid $green;
            width: 200px;
            display: none;
            &.active{
                display: flex;
            }
        }
        @media (max-width: 680px){
            left: 15px;
            right: 15px;
            transform: translate(0);
            width: auto;
            top: 135px;
            padding-left: 10px;
            padding-right: 10px;
        }
    }
    &__line{
        width: 1px;
        height: 265px;
        background-color: $grey;
        opacity: .5;
        margin-left: 30px;
        @media (max-width: 680px){
            margin-left: 10px;
        }
    }
    &__element{
        padding-left: 24px;
        li{
            margin-bottom: 25px;
        }
        &-title{
            font-size: 14px;
            line-height: 16px;
            font-weight: 400;
            color: $grey;
            &.active{
                font-size: 16px;
                line-height: 19px;
                color: $grey;
                font-weight: 700;
                position: relative;
                &:before{
                    position: absolute;
                    content: "";
                    width: 9px;
                    height: 9px;
                    border-radius: 50%;
                    background-color: $green;
                    box-shadow: 0px 0px 10px #00642F;
                    top: 6px;
                    left: -29px;
                }
                &:after{
                    position: absolute;
                    content: "";
                    width: 24px;
                    height: 1px;
                    background-color: $green;
                    left: -65px;
                    top: 10px;
                    @media (max-width: 1024px){
                        width: 9px;
                        left: -45px;
                    }
                    @media (max-width: 680px){
                        display: none;
                    }
                }
            }
        }
    }
    &__btn-exit{
        width: 100%;
        line-height: 49px;
        background: none;
        border: 1px solid $green;
        border-radius: 15px;
        color: $light-green;
        font-weight: 600;
        padding-left: 30px;
        position: relative;
        outline: none;
        cursor: pointer;
        &:before{
            position: absolute;
            content: "\e913";
            font-family: "icomoon";
            left: 68px;
            top: 0;
            font-size: 18px;
            font-weight: 400;
            @media (max-width: 1024px){
                left: 50px;
            }
        }
        &.mobile{
            display: none;
            @media (max-width: 680px){
                display: block;
                max-width: 270px;
                width: 100%;
                margin-top: 25px;
                line-height: 46px;
            }
        }
        @media (max-width: 1024px){
            line-height: 36px;
            width: 180px;
        }
        @media (max-width: 680px){
            display: none;
        }
    }
    &__btn-home{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background: none;
        outline: none;
        background-color: $green;
        border: none;
        position: relative;
        display: none;
        &:before{
            position: absolute;
            content: "\e918";
            font-family: "icomoon";
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            font-size: 18px;
            color: $white;
        }
        @media (max-width: 680px){
            display: block;
        }
    }
    &__data{
        background-color: #fff;
        border-radius: 15px;
        font-size: 18px;
        color: $black;
        font-weight: 500;
        margin-top: 40px;

        &-wrap{
            padding-left: 21px;
            padding-right: 21px;
            border-bottom: 1px solid $light-gray;
        }
        &-close-btn{
            @media (max-width: 680px){
                width: 40px;
                height: 40px;
                background-color: $green;
                border-radius: 50%;
                position: relative;
                margin-left: auto;
                &:before{
                    position: absolute;
                    content: "\e906";
                    font-family: "icomoon";
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    font-size: 16px;
                    color: $white;
                }
            }
        }
        &-title{
            font-size: 14px;
            line-height: 17px;
            font-weight: 700;
            color: $green;
            margin-top: 17px;
        }
        &-name{
            font-weight: 600;
            &:before{
                content: "\e920"; 
            }
        }
        &-mail{
            font-weight: 400; 
            &:before{
                content: "\e912";
            }
        }
        &-phone{
            font-weight: 400; 
            &:before{
                content: "\e917";
            }
        }
        &-company{
            font-weight: 400; 
            &:before{
                content: "\e921";
            }
        }
        &-contract{
            font-weight: 400;
            padding-right: 10px;
            &:before{
                content: "\e908";
            }
        } 
        &-name,
        &-mail,
        &-phone,
        &-company,
        &-contract{
            color: $grey;
            padding-left: 23px;
            font-size: 14px;
            line-height: 16px;
            position: relative;
            margin-top: 18px;
            display: inline-block;
            &:before{
                position: absolute;
                font-family: "icomoon";
                left: 0;
                top: 0;
                color: $green;
            }
            @media (max-width: 680px){
                display: block;
            }
        }
        &-block{
            display: none;
            @media (max-width: 680px){
                margin-top: 17px;
                display: flex;
                align-items: center;
                &-icon{
                    width: 35px;
                    height: 45px;
                    border: 1px solid $green;
                    border-radius: 3px;
                    margin-right: 15px;
                    position: relative;
                    &:before{
                        position: absolute;
                        content: "\e908";
                        font-family: "icomoon";
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        color: $green;
                        font-size: 28px;
                    }
                }
                &-item{
                    p{
                        font-size: 14px;
                        line-height: 17px;
                        color: $grey;
                        font-weight: 600;
                    }
                    span{
                        font-size: 14px;
                        line-height: 16px;
                        color: $grey;
                        font-weight: 400;
                    }
                }
            }
        }
        
        &-change,
        &-password{
            color: $green;
            font-size: 14px;
            line-height: 16px;
            font-weight: 400;
            display: inline-block;
            border-bottom: 1px solid $green;
        } 
        &-change{
            margin-top: 18px;
            @media (max-width: 680px){
                display: table;
            }
        }  
        &-password{
            margin-top: 14px;
            margin-bottom: 20px;
        }
        &-manager{
            padding-left: 21px;
            padding-right: 21px;
            padding-bottom: 20px;
        }
        &-btn{
            font-size: 16px;
            line-height: 19px;
            font-weight: 600;
            color: $grey;
            display: none;
            @media (max-width: 1024px){
                display: block;
                position: relative;
                &:after{
                    position: absolute;
                    content: "";
                    width: 20px;
                    height: 20px;
                    border: 1px solid $green;
                    border-radius: 50%;
                    right: -30px;
                    top: 50%;
                    transform: translate(0, -50%);
                    background-color: green;
                }
                &:before{
                    position: absolute;
                    content: "\f107";
                    font-family: "icomoon";
                    right: -25px;
                    top: 1px;
                    font-size: 18px;
                    color: $white;
                    transition: transform .3s;
                    font-weight: 400;
                    z-index: 10;
                }
                &.is-active{
                    &:before{
                        transform: rotate(-180deg);
                    }
                }
            }
            @media (max-width: 680px){
                position: absolute;
                left: 50%;
                top: 200px;
                transform: translate(-50%);
            }
        }
        @media (max-width: 1024px){
            position: absolute;
            z-index: 5;
            width: 216px;
            top: 195px;
            border: 1px solid $green;
            display: none;
            margin-top: 0px;
            &.active{
                display: block;
                
            }
        }
        @media (max-width: 680px){
            width: auto;
            left: 15px;
            right: 15px;
            top: 135px;
        }
    }
    &__element{
        &-wrap{
            display: flex;
            @media (max-width: 680px){
                margin-top: 30px;
            }
            
        }
    }
}